import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    playback: {
      active: {
        col: 0,
        el: 0,
        chain: 0
      },
      isPlayed: false,
      tempo: 60
    },
    chains: {
      current: [] as string[],
      all: [
        ['rrrr', 'rllr', 'lrrr', 'llll'],
        ['rlrr', 'llrr', 'rrrl', 'llll'],
        ['rllr', 'rlll', 'lrll', 'rlrr'],
        ['rrlr', 'rrll', 'rlrr', 'rrlr'],
        ['lrll', 'lrlr', 'rrrr', 'rllr'],
        ['LRLR', 'LLLL', 'RLLL', 'LRLR'],
        ['LLRL', 'LRLR', 'RLRL', 'RRLR'],
        ['LRLL', 'RRRR', 'RRLR', 'LLRL'],
        ['RLRR', 'LRRR', 'LLLL', 'LRLL'],
        ['LLRL', 'RRLL', 'RLLL', 'LRRL'],
        ['Lrrr', 'Lrrr', 'LrrL', 'rLLr'],
        ['rrLr', 'rrrL', 'LLLL', 'rrrL'],
        ['rrLL', 'LLLr', 'rrLr', 'rLLL'],
        ['rrrr', 'LrLL', 'rrrr', 'LrLL'],
        ['rrLL', 'LrLL', 'rrLr', 'LLrr'],
        ['RrrR', 'RrRR', 'RrRr', 'rRrr'],
        ['rrrr', 'RrRr', 'rrrR', 'Rrrr'],
        ['rRrR', 'rrRR', 'rrRR', 'rrrr'],
        ['rRRR', 'rRrR', 'RRRR', 'RRrR'],
        ['rrrr', 'RRrR', 'RrrR', 'rRRR'],
        ['LlLl', 'LLlL', 'lLlL', 'LlLL'],
        ['LLll', 'LLLL', 'llLl', 'LllL'],
        ['LLLl', 'LLLL', 'Llll', 'LLlL'],
        ['lllL', 'Llll', 'LLLL', 'LLLl'],
        ['Llll', 'LlLL', 'LLLL', 'LllL'],
        ['RRll', 'lllR', 'RllR', 'RRlR'],
        ['RRll', 'RRll', 'RRRR', 'llll'],
        ['RRRR', 'llll', 'Rlll', 'lRll'],
        ['RlRl', 'RRlR', 'RlRR', 'lRRR'],
        ['RRlR', 'RRlR', 'RlRl', 'RllR'],
        ['lrRl', 'llRl', 'rrrl', 'Rlll'],
        ['lRrR', 'rrlR', 'lRrr', 'llrl'],
        ['lRlR', 'RllR', 'rrRR', 'lRrl'],
        ['rlrr', 'RlRl', 'Rlrr', 'lrRl'],
        ['lllR', 'rRlR', 'RRlR', 'rRlR'],
        ['rllR', 'rRrr', 'rrRR', 'RrrR'],
        ['rlrl', 'lrrr', 'lRlr', 'lrlr'],
        ['rRlR', 'RrrR', 'rllr', 'rrrr'],
        ['RrrR', 'Rlrr', 'lrrl', 'llrr'],
        ['lrRl', 'llrr', 'RlRr', 'lRrl'],
        ['LrLL', 'Lllr', 'llLL', 'LrLL'],
        ['lrLL', 'rlLl', 'LrLL', 'llLr'],
        ['lrrL', 'lLrL', 'lrrl', 'rrLl'],
        ['rlLl', 'LlLL', 'lrLL', 'llrr'],
        ['rLlr', 'lrrl', 'llLL', 'rrLl'],
        ['Llrl', 'rrrr', 'llrr', 'llrl'],
        ['LLll', 'rrrL', 'lLrL', 'rrll'],
        ['rrLl', 'lrLL', 'lrlr', 'rllL']
      ]
    }
  },
  getters: {},
  mutations: {
    setChain(state, num) {
      state.chains.current = state.chains.all[num]
    },
    togglePlayback(state) {
      state.playback.isPlayed = !state.playback.isPlayed
    },
    pause(state) {
      state.playback.isPlayed = false
    },
    changeActiveElement(state) {
      if (state.chains.current) {
        if (
          state.playback.active.el <
          state.chains.current[state.playback.active.el].length - 1
        ) {
          state.playback.active.el++
        } else if (
          state.playback.active.col <
          state.chains.current.length - 1
        ) {
          state.playback.active.el = 0
          state.playback.active.col++
        } else {
          // TODO: make loop
          state.playback.active.col = 0
          state.playback.active.el = 0
          state.playback.active.chain++
          if (state.playback.active.chain >= state.chains.all.length) {
            // state.playback.active.chain = 0
            state.playback.isPlayed = false
          }
          state.chains.current = state.chains.all[state.playback.active.chain]
        }
      }
    },
    setTempo(state, value) {
      state.playback.tempo = parseInt(value)
    },
    setActive(state, { col, el }) {
      state.playback.isPlayed = false
      state.playback.active.col = col
      state.playback.active.el = el
    },
    next(state) {
      state.playback.isPlayed = false
      state.playback.active.col = 0
      state.playback.active.el = 0
      state.playback.active.chain++
      state.chains.current = state.chains.all[state.playback.active.chain]
    },
    previous(state) {
      state.playback.isPlayed = false
      state.playback.active.col = 0
      state.playback.active.el = 0
      state.playback.active.chain--
      state.chains.current = state.chains.all[state.playback.active.chain]
    },
    reload(state) {
      state.playback.isPlayed = false
      state.playback.active.col = 0
      state.playback.active.el = 0
      state.playback.active.col = 0
      state.playback.active.chain = 0
      state.chains.current = state.chains.all[state.playback.active.chain]
    }
  },
  actions: {},
  modules: {}
})
