
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  name: 'DrumChainControl',
  computed: {
    isPlayed() {
      return this.$store.state.playback.isPlayed
    },
    tempo: {
      get() {
        return this.$store.state.playback.tempo
      },
      set(value: number | undefined) {
        if (value && value > 0) {
          this.$store.commit('setTempo', value)
        }
      }
    },
    activeChain() {
      return this.$store.state.playback.active.chain
    },
    chainsCount() {
      return this.$store.state.chains.all.length
    }
  },
  methods: {
    getIcon(): 'play-fill' | 'pause-fill' {
      return this.isPlayed ? 'pause-fill' : 'play-fill'
    },
    togglePlayback() {
      this.$store.commit('togglePlayback')
    },
    previous() {
      this.$store.commit('previous')
    },
    next() {
      this.$store.commit('next')
    },
    reload() {
      this.$store.commit('reload')
    }
  }
})
