
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  name: 'DrumChainElement',
  data() {
    return {
      // isActive: false
    }
  },
  props: {
    char: {
      type: String,
      required: true
    },
    position: {
      type: Array as () => number[],
      required: true
    },
    isActive: {
      type: Boolean
    }
  },
  computed: {
    active(): boolean {
      return this.$store.state.playback.active
    }
  },
  methods: {
    getLimb(): 'arm' | 'leg' {
      return this.char === this.char.toLowerCase() ? 'arm' : 'leg'
    },
    renderedChar(): string {
      return this.char.toUpperCase()
    },
    setActive() {
      const [col, el] = this.position as number[]

      this.$store.commit('setActive', { col, el })
    }
  }
})
