
import { defineComponent } from '@vue/composition-api'
import DrumChainElement from './DrumChainElement.vue'
import { useSound } from '@vueuse/sound'

// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import tick from '@/assets/sounds/Perc_Stick_hi.wav'

export default defineComponent({
  name: 'DrumChain',
  setup() {
    const { play, stop } = useSound(tick)

    return {
      play,
      stop
    }
  },
  components: {
    DrumChainElement
  },
  data() {
    return {
      interval: null as ReturnType<typeof setInterval> | null,
      sound: null as HTMLAudioElement | null
    }
  },
  props: {
    chain: {
      type: Array as () => string[],
      required: false
    }
  },
  computed: {
    active(): number {
      return this.$store.state.playback.active
    },
    tempo(): number {
      return this.$store.state.playback.tempo
    },
    isPlayed(): boolean {
      return this.$store.state.playback.isPlayed
    }
  },
  watch: {
    isPlayed(newValue) {
      if (newValue) {
        this.play()
        this.interval = setInterval(() => {
          this.stop()
          this.changeActiveElement()
          this.play()
        }, (60 / this.tempo) * 1000)
      } else {
        this.interval && clearInterval(this.interval)
      }
    },
    tempo() {
      this.$store.commit('pause')
    }
  },
  mounted() {
    // this.sound = new Audio(tick)
  },
  methods: {
    splitSubChains(): string[][] | undefined {
      return this.chain?.map((subchain) => subchain.split(''))
    },
    changeActiveElement() {
      this.$store.commit('changeActiveElement')
    }
  }
})
